import { FILE_UPDATE, FILE_REMOVE } from '../actions';
import updateFile from '../commands/updateFile';
import removeFile from '../commands/removeFile';

export default function onAction(action, controller, next) {
  const { type, payload } = action;
  if (type === FILE_UPDATE) {
    const { node, data } = payload;
    return updateFile(controller, node, data);
  }
  if (type === FILE_REMOVE) {
    const { node } = payload;
    return removeFile(controller, node);
  }

  return next();
}
